import React from "react";

import Button from "../button/Button";

import "./catalogueEmpty.scss";
import imageWebp from "../../assets/images/product-page-error/error_img.webp";
import imageSrc from "../../assets/images/product-page-error/error_img.jpg";

interface CatalogueEmptyProps {
	onResetQuery: () => void;
}

const CatalogueEmpty: React.FC<CatalogueEmptyProps> = ({ onResetQuery }) => {
	return (
		<div className="catalogue-empty">
			<picture className="catalogue-empty__image">
				<source srcSet={imageWebp} type="image/webp" />
				<img src={imageSrc} alt="Сталася помилка" />
			</picture>
			<div className="catalogue-empty__content">
				<p className="catalogue-empty__title">Відсутні товари, що відповідають Вашим критеріям пошуку</p>
				<p className="catalogue-empty__subtitle">
					Пропонуємо Вам змінити критерії пошуку, скинути всі фільтри або повернутися на головну сторінку.
				</p>
				<div className="catalogue-empty__buttons">
					<Button className="catalogue-empty__button-home" href="/" title="На головну" />
					<Button
						className="catalogue-empty__button-retry"
						title="Скинути фільтри"
						aria-label="скинути всі фільтри"
						onClick={onResetQuery}
					/>
				</div>
			</div>
		</div>
	);
};

export default CatalogueEmpty;
