import React from "react";
import { motion } from "framer-motion";

import { IFilter } from "../../types";

import "./filtersCatalogue.scss";

interface FiltersCatalogueProps {
	selectedFilter: number;
	filters: IFilter[];
	onChange: (value: IFilter["id"]) => void;
	groupName: string;
}

const FiltersCatalogue: React.FC<FiltersCatalogueProps> = ({ selectedFilter, filters, onChange, groupName }) => {
	return (
		<div className="filters-catalogue">
			<ul className="filters-catalogue__list">
				{filters.map((filter) => (
					<li className="filters-catalogue__item" key={filter.id}>
						<label className="filters-catalogue__label">
							<input
								className="filters-catalogue__input visually-hidden"
								type="radio"
								name={groupName}
								aria-label={`обрати ${filter.name} як активний фільтр`}
								value={filter.label}
								checked={filter.id === selectedFilter}
								onChange={() => onChange(filter.id)}
							/>
							<span>
								<span className="filters-catalogue__item-title">{filter.name}</span>
								{filter.id === selectedFilter && (
									<motion.span
										className="filters-catalogue__item-bg"
										layoutId="filtersCatalogueItemBg"
									/>
								)}
							</span>
						</label>
					</li>
				))}
			</ul>
		</div>
	);
};

export default FiltersCatalogue;
