import React from "react";
import { Link } from "react-router-dom";

import { Pagination, PaginationItem } from "@mui/material";

import "./cataloguePagination.scss";

interface CataloguePaginationProps {
	pagesCount: number;
	onChangePage: (value: number) => void;
	searchParams: URLSearchParams;
	currentPage?: number;
}

const CataloguePagination: React.FC<CataloguePaginationProps> = ({
	pagesCount,
	onChangePage,
	searchParams,
	currentPage = 1,
}) => {
	const createPaginationItemLink = (page: number | null): string => {
		const filter = searchParams.get("filter");
		const paramsObj: { page?: string; filter?: string } = {};

		if (filter) {
			paramsObj.filter = filter;
		}

		if (page && page !== 1) {
			paramsObj.page = page.toString();
		}

		const queryParams = new URLSearchParams(paramsObj);

		if (queryParams.has("page") || queryParams.has("filter")) {
			return `/catalogue?${queryParams.toString()}`;
		}

		return "/catalogue";
	};

	return (
		<Pagination
			className="catalogue-pagination"
			count={pagesCount}
			onChange={(_, page) => onChangePage(page)}
			page={currentPage}
			renderItem={(item) => (
				<PaginationItem
					className="catalogue-pagination__item"
					component={Link}
					to={createPaginationItemLink(item.page)}
					{...item}
				/>
			)}
		/>
	);
};

export default CataloguePagination;
