import React from "react";
import { useSearchParams } from "react-router-dom";
import { Helmet } from "react-helmet";

import { IFilter, ProductLabels } from "../../types";
import { useGetProductsQuery, useGetPagesQuantityQuery } from "../../services/mockApi";
import FiltersCatalogue from "../../components/filtersCatalogue/FiltersCatalogue";
import CatalogueList from "../../components/catalogueList/CatalogueList";
import SectionProductsError from "../../components/sectionProductsError/SectionProductsError";
import CataloguePagination from "../../components/cataloguePagination/CataloguePagination";
import CatalogueEmpty from "../../components/catalogueEmpty/CatalogueEmpty";
import { scrollToTop } from "../../services";

import "./cataloguePage.scss";

const filters: IFilter[] = [
	{
		id: 0,
		label: ProductLabels.DEFAULT,
		name: "Всі",
	},
	{
		id: 1,
		label: ProductLabels.HIT,
		name: "Хіти продажів",
	},
	{
		id: 2,
		label: ProductLabels.DISCOUNT,
		name: "Зі знижкою",
	},
	{
		id: 3,
		label: ProductLabels.SOON,
		name: "Скоро в наявності",
	},
	{
		id: 4,
		label: ProductLabels.NEW_TASTE,
		name: "Нові смаки",
	},
];

const CataloguePage: React.FC = () => {
	const [searchParams, setSearchParams] = useSearchParams();

	const itemsPerPage = 6;
	const [currentPage, setCurrentPage] = React.useState<number>(getPageFromQuery());
	const [totalPagesQuantity, setTotalPagesQuantity] = React.useState<number>(1);

	const [activeFilter, setActiveFilter] = React.useState<IFilter["id"]>(getFilterIdFromQuery());
	// fetching pages quantity for the selected filter
	const { data: pages } = useGetPagesQuantityQuery({
		limit: itemsPerPage,
		[getFilterLabelById(activeFilter)]: true,
	});
	// fetching products
	const { data, isError, isFetching, refetch } = useGetProductsQuery({
		page: currentPage,
		limit: itemsPerPage,
		[getFilterLabelById(activeFilter)]: true,
	});

	const [isProductsArrayEmpty, setProductsArrayEmpty] = React.useState<boolean>(false);

	// when totalPageCount is fetched, setting new value
	React.useEffect(() => {
		if (pages) {
			setTotalPagesQuantity(pages);
		}
	}, [pages]);

	// triggering isProductsArrayEmpty when the data array is empty
	React.useEffect(() => {
		if (data?.length === 0) {
			setProductsArrayEmpty(true);
		}
	}, [data]);

	const handleChangeFilter = (value: IFilter["id"]) => {
		setCurrentPage(1);
		setActiveFilter(value);

		const filterLabel = getFilterLabelById(value);
		const params: { filter?: ProductLabels } = {};

		if (value) {
			params.filter = filterLabel;
		}

		setSearchParams(params);
	};

	const handleRefetch = () => {
		refetch();
	};

	function getPageFromQuery(): number {
		const page = searchParams.get("page");

		if (page) {
			return +page;
		}

		return 1;
	}

	function getFilterIdFromQuery(): IFilter["id"] {
		const filterLabel = searchParams.get("filter") as ProductLabels | null;

		if (filterLabel) {
			const filter = filters.find((item) => item.label === filterLabel);
			return filter?.id ?? 0;
		}

		return 0;
	}

	function getFilterLabelById(id: IFilter["id"]): ProductLabels {
		const filter = filters.find((item) => item.id === id);

		return filter?.label ?? ProductLabels.DEFAULT;
	}

	const handleChangePage = (value: number): void => {
		const params: { page?: string; filter?: string } = {};
		const filter = searchParams.get("filter");

		if (value !== 1) {
			params.page = value.toString();
		}

		if (filter) {
			params.filter = filter;
		}

		setCurrentPage(value);
		setSearchParams(params);

		window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
	};

	const resetQuery = () => {
		handleChangeFilter(0);
		setProductsArrayEmpty(false);
		scrollToTop();
	};

	return (
		<>
			<Helmet>
				<title>Каталог продуктів - Просто паста</title>
				<meta
					name="description"
					content="На сторінці можна відшукати пасту на будь який Ваш смак. Арахісова солодка, солона, з фініками, з молоком, шоколадна. Великий та малий розмір. Можна додати додаткову начинку"
				/>
			</Helmet>
			<div className="page-catalogue">
				<div className="container">
					<div className="page-catalogue__inner">
						<h1 className="page-catalogue__title title">Наша продукцiя</h1>
						<div className="page-catalogue__filters">
							<FiltersCatalogue
								selectedFilter={activeFilter}
								filters={filters}
								onChange={handleChangeFilter}
								groupName="catalogue-filters-01"
							/>
						</div>
						<div className="page-catalogue__products">
							<div className="products-catalogue">
								{isError && <SectionProductsError onRetry={handleRefetch} />}
								{isProductsArrayEmpty && (
									<div className="products-catalogue__empty">
										<CatalogueEmpty onResetQuery={resetQuery} />
									</div>
								)}
								<div className="products-catalogue__list">
									{!isError && <CatalogueList isLoading={isFetching} products={data} />}
								</div>
							</div>
						</div>
						<div className="page-catalogue__pagination">
							{!isProductsArrayEmpty && (
								<CataloguePagination
									pagesCount={totalPagesQuantity}
									onChangePage={handleChangePage}
									searchParams={searchParams}
									currentPage={currentPage}
								/>
							)}
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default CataloguePage;
